<template>
  <Alert view="smooth" dense style="align-items: center;" class="mb-4">
    <p>
      Check out Google's support resources:&nbsp;
      <a href="https://support.google.com/analytics/answer/13128484" target="_blank" class="text-blue-04 hover:text-blue-03 hover:underline">
        <b>Conversion Guide</b>
      </a>
    </p>
    <template #left>
      <span class="material-icons text-4xl">info</span>
    </template>
  </Alert>
  <Input v-model="data.google_analytic_code" :view="validation.google_analytic_code.$invalid ? 'danger' : 'default'" class="my-4 w-full" label="Measurement ID" placeholder="G-XXXXXX" />
  <small class="text-red-base block mb-4" v-for="(error, errorIndex) of validation.google_analytic_code.$errors" :key="errorIndex">{{ error.$message }}. </small>
</template>

<script>
import { defineComponent } from 'vue'
import { useShowAll } from 'composable/integration'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'
import { ElMessage } from 'element-plus'
import { isFunction } from '@/utils/is'

export default defineComponent({
  components: {
    Button,
    Alert,
    Input
  },
  async setup(){
    const { state, data, actionAll, actionUpdate, loading } = useShowAll()
    const rules = {
      google_analytic_code: {
        required
      }
    }
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const update = async (callback=()=>{}) => {
      const isValid = validate()
      if( !isValid ) return
      const payload = Object.assign({}, {
        google_analytic_code: data.google_analytic_code
      })
      await actionUpdate(payload, (response) => {
        if( response.toLowerCase() === 'connection failed' ) return ElMessage({
          type: 'warning',
          message: 'Invalid Google Analytics configurations'
        })
        ElMessage({
          type: 'success',
          message: 'Google Analytics integration updated!'
        })
        isFunction(callback) && callback()
      })
    }

    await Promise.all([
      actionAll()
    ])

    return {
      state,
      data,
      loading,
      validation,
      update
    }
  }
})
</script>